@import "../scss/global.scss";

.stay-page {
    background-color: #1c1c1c;
    height: 100vh;
    position: relative;
    z-index: 1;
}
.stay-big {
    @include en-4;
    color: #f4f4f4;
    text-align: right;
    padding: 3rem;
}

.box-text {
    color: #f4f4f4;
}

.stay-trouble-block,
.stay-next-block {
    @include preten-Regular;
    color: #f4f4f4;
}
.stay-big,
.box-text,
.stay-link {
    position: relative;
    z-index: 2;
}
.stay-next-block {
    margin-top: 10rem;
}
.stay-image {
    position: fixed;
    z-index: 0;
    top: 28%;
    left: 45%;
    width: 55%;
}
.stay-inner-text-top {
    transform: translateY(-24px);
 }
.stay-inner-text-top p {
    padding: 0.8rem 0 0 0;
}
.stay-inner-text-bottom {
    padding-top: rem;
}
.stay-trouble-block > h3 {
    @include en-4;
    color: #f4f4f4;
}
.stay-next-block > h3 {
    @include en-4;
    color: #f4f4f4;
}

.box-text p {
    opacity: 0;
    transform: translateX(-24px);
    animation: textSlide 0.45s ease-out forwards;
}

.box-text p:nth-child(1) {
    animation-delay: 0.05s;
}
.box-text p:nth-child(2) {
    animation-delay: 0.12s;
}
.box-text p:nth-child(3) {
    animation-delay: 0.19s;
}
.box-text p:nth-child(4) {
    animation-delay: 0.26s;
}
.box-text p:nth-child(5) {
    animation-delay: 0.33s;
}

@keyframes textSlide {
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

.stay-link {
    color: #f4f4f4;
    @include preten-Regular;
    display: block;
    position: fixed;
    top: 75%;
    left: 65%;
}
